/*
*  object  key, value  object  
*  @param {Object} obj  obj
*  @return {Array}
*/
const convertObjectToArray = (obj) => {
  return Object.keys(obj).map(key => {
    return { [key]: obj[key] }
  });
}

const convertObjectToArrayWithKeyValue = (obj) => {
  return Object.keys(obj).map(key => {
    return { 
      key: key,
      value: obj[key] 
    }
  });
}

export default {
  convertObjectToArray,
  convertObjectToArrayWithKeyValue
}